.main{
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: #F7D006;
}

.follower-btn{
  position: absolute;
  float: left;
  transition: 0.2s;
  background-color: #fa5a5a;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  color: #ffffff;
  text-align: center;
  font-size: 30px;
  cursor: pointer;
  padding: 25px 35px;
}